import axios from 'axios'
// axios
const http = axios.create({
  baseURL: '', // apibase_url
  /* baseURL: "proxy", */
  // `method` 
  method: 'post', //  post
  headers: {
    'Content-Type': 'application/json;charset=UTF-8'
  }
})
export default function (config) {
  return http(config)
}
