import request from '@/axios'
// 
export function register (data) {
  return request({
    url: '/register',
    data: data
  })
}
// 
export function login (data) {
  return request({
    url: '/login',
    data: data
  })
}
// 
export function createTrusts (data) {
  return request({
    url: '/trusts/create',
    data: data
  })
}
// 
export function deleteTrusts (data) {
  return request({
    url: '/trusts/delete',
    data: data
  })
}
// 
export function getTrusts (data) {
  return request({
    url: '/trusts/get',
    data: data
  })
}
